.app-container {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 200px;
  padding: 20px;
  background-color: #f4f4f4;
}

.member-sidebar, .project-sidebar {
  flex: 0 0 200px;
}

.sidebar h2 {
  font-size: 18px;
  margin-bottom: 10px;
}

.sidebar-button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 5px;
  background-color: #fff;
  border: 1px solid #ddd;
  cursor: pointer;
  text-align: left;
}

.sidebar-button:hover {
  background-color: #eee;
}

.details {
  flex: 1;
  padding: 20px;
}

.details h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.detail-section {
  margin-bottom: 20px;
}

.add-project input,
.add-project textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
}

.add-project button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.add-project button:hover {
  background-color: #0056b3;
}
